import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ReactComponent as Logo } from "../assets/logo.svg";
import { ReactComponent as HideIcon } from "../assets/password_hide.svg";
import { ReactComponent as ShowIcon } from "../assets/password_show.svg";
import "../styles/Join.css";

export default function Join() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [name, setName] = useState("");
  const [idError, setIdError] = useState(""); //   
  const [nameError, setNameError] = useState(""); //   
  const [passwordError, setPasswordError] = useState(""); //   
  const navigate = useNavigate();

  //     
  const validateId = (value) => {
    const koreanRegex = /[-|-|-]/;
    const specialCharRegex = /[^a-zA-Z0-9]/;
    const existingIds = ["test", "admin", "user"];
    if (koreanRegex.test(value) || specialCharRegex.test(value) || existingIds.includes(value)) {
      setIdError("    .   .");
    } else {
      setIdError("");
    }
  };
  const handleIdChange = (e) => {
    setEmail(e.target.value);
    validateId(e.target.value);
  };

  //    (/ +  )
  const validateName = (value) => {
    const nameRegex = /^[a-zA-Z-\s]+$/; // (\s) 
    if (!nameRegex.test(value)) {
      setNameError("    .   . (,    )");
    } else {
      setNameError("");
    }
  };
  const handleNameChange = (e) => {
    setName(e.target.value);
    validateName(e.target.value);
  };

  //    (  )
  const validatePassword = (value) => {
    const koreanRegex = /[-|-|-]/;
    if (koreanRegex.test(value)) {
      setPasswordError("    .  ,  .");
    } else {
      setPasswordError("");
    }
  };
  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
    validatePassword(e.target.value);
  };

  //  / 
  const togglePasswordVisibility = () => {
    setShowPassword((prev) => !prev);
  };

  //  
  const handleSubmit = (e) => {
    e.preventDefault();
    if (idError) {
      alert("  .");
      return;
    }
    if (nameError) {
      alert("  .");
      return;
    }
    if (passwordError) {
      alert("  .");
      return;
    }
    console.log({ email, password, name });
    alert(" .");
    navigate("/login"); //     
  };

  return (
    <div className="join_page">
      <h2><Logo className="logo_img" /></h2>
      <form onSubmit={handleSubmit}>
        <div className="input_wrapper">
          <input type="text" placeholder="  " value={email} onChange={handleIdChange} className={idError ? "input_error" : ""} required/>
          {idError && <p className="error_text">{idError}</p>}
        </div>

        <div className={`password_wrapper ${passwordError ? "error" : ""}`}>
          <input type={showPassword ? "text" : "password"} placeholder="  " value={password} onChange={handlePasswordChange} className={passwordError ? "input_error" : ""} required/>
          {showPassword ? (
            <ShowIcon className="password_icon" onClick={togglePasswordVisibility} />
          ) : (
            <HideIcon className="password_icon" onClick={togglePasswordVisibility} />
          )}
          {passwordError && <p className="error_text">{passwordError}</p>}
        </div>

        <div className="input_wrapper">
          <input type="text" placeholder=" " value={name} onChange={handleNameChange} className={nameError ? "input_error" : ""} required/>
          {nameError && <p className="error_text">{nameError}</p>}
        </div>

        <button type="submit" className="join"></button>
      </form>
    </div>
  );
}
